import { useState, useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Users, Circle, RefreshCw, MapPin } from 'lucide-react';
import { useOnlineUsers } from '@/hooks/useOnlineUsers';
import { formatDistanceToNow } from 'date-fns';
import { zhCN, enUS, pt } from 'date-fns/locale';
import { toast } from 'sonner';

interface OnlineUsersDialogProps {
  trigger?: React.ReactNode;
}

export const OnlineUsersDialog = ({ trigger }: OnlineUsersDialogProps) => {
  const { t, i18n } = useTranslation();
  const [open, setOpen] = useState(false);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const { onlineUsers, onlineCount, isLoading, refetch } = useOnlineUsers();

  const handleRefresh = useCallback(async () => {
    setIsRefreshing(true);
    try {
      await refetch();
      toast.success(t('common.refreshed', '已刷新'));
    } finally {
      setIsRefreshing(false);
    }
  }, [refetch, t]);

  const getLocale = () => {
    switch (i18n.language) {
      case 'zh': return zhCN;
      case 'pt': return pt;
      default: return enUS;
    }
  };

  const getRoleBadgeVariant = (role: string | undefined) => {
    switch (role) {
      case 'admin': return 'destructive';
      case 'supervisor': return 'default';
      case 'merchant': return 'secondary';
      default: return 'outline';
    }
  };

  const getRoleLabel = (role: string | undefined) => {
    switch (role) {
      case 'admin': return t('admin.role.admin', '管理员');
      case 'supervisor': return t('admin.role.supervisor', '主管');
      case 'merchant': return t('admin.role.merchant', '商家');
      default: return t('admin.role.user', '用户');
    }
  };

  const formatLocation = (city: string | null | undefined, country: string | null | undefined) => {
    if (city && country) {
      return `${city}, ${country}`;
    }
    return city || country || null;
  };

  const hasLocationData = (user: typeof onlineUsers[0]) => {
    return user.city || user.country || user.ip_address;
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        {trigger || (
          <Button variant="outline" size="sm">
            <Users className="w-4 h-4 mr-2" />
            {t('admin.onlineUsers.viewOnline', '查看在线用户')}
          </Button>
        )}
      </DialogTrigger>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <Circle className="w-3 h-3 fill-green-500 text-green-500" />
            {t('admin.onlineUsers.title', '在线用户')} ({onlineCount})
          </DialogTitle>
        </DialogHeader>
        
        <div className="flex justify-end mb-2">
          <Button 
            variant="ghost" 
            size="sm" 
            onClick={handleRefresh} 
            disabled={isLoading || isRefreshing}
          >
            <RefreshCw className={`w-4 h-4 mr-1 ${(isLoading || isRefreshing) ? 'animate-spin' : ''}`} />
            {t('common.refresh', '刷新')}
          </Button>
        </div>

        <ScrollArea className="h-[400px] pr-4">
          {isLoading ? (
            <div className="flex items-center justify-center h-32 text-muted-foreground">
              {t('common.loading', '加载中...')}
            </div>
          ) : onlineUsers.length === 0 ? (
            <div className="flex items-center justify-center h-32 text-muted-foreground">
              {t('admin.onlineUsers.noOnline', '暂无在线用户')}
            </div>
          ) : (
            <div className="space-y-3">
              {onlineUsers.map((user) => (
                <div
                  key={user.user_id}
                  className="flex items-center gap-3 p-3 rounded-lg bg-muted/50 hover:bg-muted transition-colors"
                >
                  <div className="relative">
                    <Avatar className="h-10 w-10">
                      <AvatarImage src={user.profile?.avatar_url || undefined} />
                      <AvatarFallback>
                        {user.profile?.display_name?.charAt(0) || '?'}
                      </AvatarFallback>
                    </Avatar>
                    <Circle 
                      className={`absolute -bottom-0.5 -right-0.5 w-3 h-3 ${
                        user.is_online 
                          ? 'fill-green-500 text-green-500' 
                          : 'fill-yellow-500 text-yellow-500'
                      }`} 
                    />
                  </div>
                  
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2">
                      <span className="font-medium truncate">
                        {user.profile?.display_name || t('common.anonymous', '匿名用户')}
                      </span>
                      <Badge variant={getRoleBadgeVariant(user.role)} className="text-xs">
                        {getRoleLabel(user.role)}
                      </Badge>
                    </div>
                    <p className="text-xs text-muted-foreground">
                      {user.is_online 
                        ? t('admin.onlineUsers.online', '在线中')
                        : t('admin.onlineUsers.lastSeen', '最后活跃 {{time}}', {
                            time: formatDistanceToNow(new Date(user.last_seen), {
                              addSuffix: true,
                              locale: getLocale()
                            })
                          })
                      }
                    </p>
                    {hasLocationData(user) && (
                      <div className="flex items-center gap-1 mt-1 text-xs text-muted-foreground">
                        <MapPin className="w-3 h-3" />
                        <span className="truncate">
                          {formatLocation(user.city, user.country) && (
                            <span>{formatLocation(user.city, user.country)}</span>
                          )}
                          {user.ip_address && (
                            <>
                              {formatLocation(user.city, user.country) && <span className="mx-1">·</span>}
                              <span className="font-mono text-[10px]">{user.ip_address}</span>
                            </>
                          )}
                        </span>
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          )}
        </ScrollArea>
      </DialogContent>
    </Dialog>
  );
};
