import { useState, useEffect } from 'react';
import { supabase } from '@/integrations/supabase/client';
import { useQuery } from '@tanstack/react-query';
import { RealtimeChannel } from '@supabase/supabase-js';

interface OnlineUser {
  user_id: string;
  is_online: boolean;
  last_seen: string;
  ip_address?: string | null;
  city?: string | null;
  country?: string | null;
  profile?: {
    display_name: string;
    avatar_url: string | null;
  };
  role?: string;
}

interface OnlineVisitor {
  visitor_id: string;
  online_at: string;
  country?: string | null;
  city?: string | null;
}

interface PresenceState {
  [key: string]: { user_id: string; online_at: string }[];
}

interface VisitorPresenceState {
  [key: string]: { visitor_id: string; online_at: string; country?: string; city?: string }[];
}

export const useOnlineUsers = () => {
  const [realtimeOnlineIds, setRealtimeOnlineIds] = useState<Set<string>>(new Set());
  const [realtimeVisitorIds, setRealtimeVisitorIds] = useState<Set<string>>(new Set());
  const [visitorDetails, setVisitorDetails] = useState<OnlineVisitor[]>([]);
  const [channel, setChannel] = useState<RealtimeChannel | null>(null);
  const [visitorChannel, setVisitorChannel] = useState<RealtimeChannel | null>(null);

  // Subscribe to presence channel to get real-time online status for users
  useEffect(() => {
    console.log('Initializing online users observer...');
    
    const presenceChannel = supabase.channel('online-users');

    presenceChannel
      .on('presence', { event: 'sync' }, () => {
        const state = presenceChannel.presenceState() as PresenceState;
        const onlineIds = new Set<string>();
        
        Object.keys(state).forEach(key => {
          onlineIds.add(key);
        });
        
        setRealtimeOnlineIds(onlineIds);
        console.log('Presence sync - online users:', Array.from(onlineIds));
      })
      .on('presence', { event: 'join' }, ({ key, newPresences }) => {
        console.log('User joined:', key, newPresences);
        setRealtimeOnlineIds(prev => new Set([...prev, key]));
      })
      .on('presence', { event: 'leave' }, ({ key, leftPresences }) => {
        console.log('User left:', key, leftPresences);
        setRealtimeOnlineIds(prev => {
          const next = new Set(prev);
          next.delete(key);
          return next;
        });
      })
      .subscribe((status) => {
        console.log('Presence channel status:', status);
      });

    setChannel(presenceChannel);

    return () => {
      console.log('Cleaning up online users observer...');
      supabase.removeChannel(presenceChannel);
    };
  }, []);

  // Subscribe to visitor presence channel
  useEffect(() => {
    console.log('Initializing online visitors observer...');
    
    const visitorsChannel = supabase.channel('online-visitors');

    visitorsChannel
      .on('presence', { event: 'sync' }, () => {
        const state = visitorsChannel.presenceState() as VisitorPresenceState;
        const visitorIds = new Set<string>();
        const visitors: OnlineVisitor[] = [];
        
        Object.entries(state).forEach(([key, presences]) => {
          visitorIds.add(key);
          if (presences && presences.length > 0) {
            const presence = presences[0];
            visitors.push({
              visitor_id: key,
              online_at: presence.online_at,
              country: presence.country,
              city: presence.city,
            });
          }
        });
        
        setRealtimeVisitorIds(visitorIds);
        setVisitorDetails(visitors);
        console.log('Visitor presence sync - online visitors:', Array.from(visitorIds));
      })
      .on('presence', { event: 'join' }, ({ key, newPresences }) => {
        console.log('Visitor joined:', key, newPresences);
        setRealtimeVisitorIds(prev => new Set([...prev, key]));
        if (newPresences && newPresences.length > 0) {
          const presence = newPresences[0] as unknown as { visitor_id: string; online_at: string; country?: string; city?: string };
          setVisitorDetails(prev => [
            ...prev.filter(v => v.visitor_id !== key),
            {
              visitor_id: key,
              online_at: presence.online_at,
              country: presence.country,
              city: presence.city,
            }
          ]);
        }
      })
      .on('presence', { event: 'leave' }, ({ key, leftPresences }) => {
        console.log('Visitor left:', key, leftPresences);
        setRealtimeVisitorIds(prev => {
          const next = new Set(prev);
          next.delete(key);
          return next;
        });
        setVisitorDetails(prev => prev.filter(v => v.visitor_id !== key));
      })
      .subscribe((status) => {
        console.log('Visitor presence channel status:', status);
      });

    setVisitorChannel(visitorsChannel);

    return () => {
      console.log('Cleaning up online visitors observer...');
      supabase.removeChannel(visitorsChannel);
    };
  }, []);

  // Fetch user details from database (profiles, roles, location info)
  const { data: userDetails, isLoading, refetch: refetchDetails } = useQuery({
    queryKey: ['online-users-details', Array.from(realtimeOnlineIds).sort().join(',')],
    queryFn: async () => {
      if (realtimeOnlineIds.size === 0) {
        return [];
      }

      const userIds = Array.from(realtimeOnlineIds);
      console.log('Fetching details for online users:', userIds);
      
      // Get presence data for location info
      const { data: presenceData, error: presenceError } = await supabase
        .from('user_presence')
        .select('*')
        .in('user_id', userIds);

      if (presenceError) {
        console.error('Error fetching presence:', presenceError);
      }

      // Get user profiles
      const { data: profiles, error: profilesError } = await supabase
        .rpc('get_public_profiles', { user_ids: userIds });

      if (profilesError) {
        console.error('Error fetching profiles:', profilesError);
      }

      // Get user roles
      const { data: roles, error: rolesError } = await supabase
        .from('user_roles')
        .select('user_id, role')
        .in('user_id', userIds);

      if (rolesError) {
        console.error('Error fetching roles:', rolesError);
      }

      const presenceMap = new Map((presenceData || []).map(p => [p.user_id, p]));
      const profileMap = new Map((profiles || []).map(p => [p.id, p]));
      const roleMap = new Map((roles || []).map(r => [r.user_id, r.role]));

      return userIds.map(userId => {
        const presence = presenceMap.get(userId);
        const profile = profileMap.get(userId);
        
        return {
          user_id: userId,
          is_online: true,
          last_seen: presence?.last_seen || new Date().toISOString(),
          ip_address: presence?.ip_address,
          city: presence?.city,
          country: presence?.country,
          profile: profile ? {
            display_name: profile.display_name,
            avatar_url: profile.avatar_url
          } : undefined,
          role: roleMap.get(userId)
        };
      });
    },
    enabled: realtimeOnlineIds.size > 0,
    staleTime: 10000,
  });

  // Fallback: Also check database for recently active users
  const { data: dbOnlineUsers, refetch: refetchFallback } = useQuery({
    queryKey: ['online-users-db-fallback'],
    queryFn: async () => {
      const twoMinutesAgo = new Date(Date.now() - 2 * 60 * 1000).toISOString();
      
      const { data: presenceData, error } = await supabase
        .from('user_presence')
        .select('*')
        .gte('last_seen', twoMinutesAgo)
        .order('last_seen', { ascending: false });

      if (error) {
        console.error('Error fetching DB presence:', error);
        return [];
      }

      if (!presenceData || presenceData.length === 0) {
        return [];
      }

      const userIds = presenceData.map(p => p.user_id);
      
      const { data: profiles } = await supabase
        .rpc('get_public_profiles', { user_ids: userIds });

      const { data: roles } = await supabase
        .from('user_roles')
        .select('user_id, role')
        .in('user_id', userIds);

      const profileMap = new Map((profiles || []).map(p => [p.id, p]));
      const roleMap = new Map((roles || []).map(r => [r.user_id, r.role]));

      return presenceData.map(presence => ({
        user_id: presence.user_id,
        is_online: presence.is_online,
        last_seen: presence.last_seen,
        ip_address: presence.ip_address,
        city: presence.city,
        country: presence.country,
        profile: profileMap.get(presence.user_id) ? {
          display_name: profileMap.get(presence.user_id)!.display_name,
          avatar_url: profileMap.get(presence.user_id)!.avatar_url
        } : undefined,
        role: roleMap.get(presence.user_id)
      }));
    },
    refetchInterval: 30000,
  });

  // Merge realtime and DB users
  const mergedUsers = (() => {
    const userMap = new Map<string, OnlineUser>();
    
    (dbOnlineUsers || []).forEach(user => {
      userMap.set(user.user_id, user);
    });
    
    (userDetails || []).forEach(user => {
      userMap.set(user.user_id, { ...user, is_online: true });
    });
    
    realtimeOnlineIds.forEach(userId => {
      if (!userMap.has(userId)) {
        userMap.set(userId, {
          user_id: userId,
          is_online: true,
          last_seen: new Date().toISOString(),
        });
      }
    });
    
    return Array.from(userMap.values());
  })();

  const onlineCount = mergedUsers.length;
  const visitorCount = realtimeVisitorIds.size;

  const refetch = async () => {
    await Promise.all([refetchDetails(), refetchFallback()]);
  };

  return {
    onlineUsers: mergedUsers,
    onlineCount,
    onlineVisitors: visitorDetails,
    visitorCount,
    isLoading,
    refetch
  };
};

export const useUserOnlineStatus = (userId: string | undefined) => {
  const [isOnline, setIsOnline] = useState(false);

  useEffect(() => {
    if (!userId) {
      setIsOnline(false);
      return;
    }

    const channel = supabase.channel('online-users');

    channel
      .on('presence', { event: 'sync' }, () => {
        const state = channel.presenceState();
        setIsOnline(!!state[userId]);
      })
      .on('presence', { event: 'join' }, ({ key }) => {
        if (key === userId) {
          setIsOnline(true);
        }
      })
      .on('presence', { event: 'leave' }, ({ key }) => {
        if (key === userId) {
          setIsOnline(false);
        }
      })
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, [userId]);

  return { isOnline, isLoading: false };
};
