import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { startOfDay, subDays, format } from "date-fns";
import { fetchAllPaginated } from "@/lib/paginatedQuery";

interface RefererStats {
  domain: string;
  count: number;
  percentage: number;
}

interface LocationStats {
  location: string;
  count: number;
  percentage: number;
}

interface LocationDailyView {
  date: string;
  location: string;
  views: number;
}

interface PageViewStats {
  totalViews: number;
  uniqueVisitors: number;
  viewsByPage: Record<string, number>;
  dailyViews: Array<{ date: string; views: number; uniqueVisitors: number }>;
  topRestaurants: Array<{ id: string; name: string; views: number }>;
  topActivities: Array<{ id: string; title: string; views: number }>;
  refererStats: RefererStats[];
  countryStats: LocationStats[];
  cityStats: LocationStats[];
  countryDailyViews: LocationDailyView[];
  cityDailyViews: LocationDailyView[];
}

// Parse referer URL to get domain
const getRefererDomain = (referer: string | null): string => {
  if (!referer) return "direct";
  try {
    const url = new URL(referer);
    return url.hostname.replace("www.", "");
  } catch {
    return "unknown";
  }
};

export const useAnalytics = (days: number = 30) => {
  return useQuery({
    queryKey: ["analytics", days],
    queryFn: async (): Promise<PageViewStats> => {
      // Calculate date filter
      let dateFilter: string | null = null;
      if (days === 1) {
        dateFilter = startOfDay(new Date()).toISOString();
      } else if (days > 1) {
        dateFilter = startOfDay(subDays(new Date(), days - 1)).toISOString();
      }

      // Fetch all data using pagination to overcome 1000 row limit
      const views = await fetchAllPaginated(
        "page_views",
        "*",
        (query) => {
          if (dateFilter) {
            query = query.gte("created_at", dateFilter);
          }
          return query.order("created_at", { ascending: false });
        }
      );

      // Calculate total views
      const totalViews = views.length;

      // Calculate unique visitors
      const uniqueVisitorIds = new Set(views.map(v => v.visitor_id));
      const uniqueVisitors = uniqueVisitorIds.size;

      // Views by page type
      const viewsByPage: Record<string, number> = {};
      views.forEach(v => {
        viewsByPage[v.page_type] = (viewsByPage[v.page_type] || 0) + 1;
      });

      // Daily views
      const dailyMap = new Map<string, { views: number; visitors: Set<string> }>();
      views.forEach(v => {
        const date = format(new Date(v.created_at), "yyyy-MM-dd");
        if (!dailyMap.has(date)) {
          dailyMap.set(date, { views: 0, visitors: new Set() });
        }
        const day = dailyMap.get(date)!;
        day.views++;
        day.visitors.add(v.visitor_id);
      });

      const dailyViews = Array.from(dailyMap.entries())
        .map(([date, data]) => ({
          date,
          views: data.views,
          uniqueVisitors: data.visitors.size,
        }))
        .sort((a, b) => a.date.localeCompare(b.date));

      // Referer statistics
      const refererMap = new Map<string, number>();
      views.forEach(v => {
        const domain = getRefererDomain(v.referer);
        refererMap.set(domain, (refererMap.get(domain) || 0) + 1);
      });

      const refererStats: RefererStats[] = Array.from(refererMap.entries())
        .map(([domain, count]) => ({
          domain,
          count,
          percentage: totalViews > 0 ? (count / totalViews) * 100 : 0,
        }))
        .sort((a, b) => b.count - a.count);

      // Country statistics
      const countryMap = new Map<string, number>();
      views.forEach(v => {
        const country = (v as any).country || "unknown";
        countryMap.set(country, (countryMap.get(country) || 0) + 1);
      });

      const countryStats: LocationStats[] = Array.from(countryMap.entries())
        .map(([location, count]) => ({
          location,
          count,
          percentage: totalViews > 0 ? (count / totalViews) * 100 : 0,
        }))
        .sort((a, b) => b.count - a.count);

      // City statistics
      const cityMap = new Map<string, number>();
      views.forEach(v => {
        const city = (v as any).city || "unknown";
        cityMap.set(city, (cityMap.get(city) || 0) + 1);
      });

      const cityStats: LocationStats[] = Array.from(cityMap.entries())
        .map(([location, count]) => ({
          location,
          count,
          percentage: totalViews > 0 ? (count / totalViews) * 100 : 0,
        }))
        .sort((a, b) => b.count - a.count);

      // Country daily views (for trend analysis)
      const countryDailyMap = new Map<string, Map<string, number>>();
      views.forEach(v => {
        const date = format(new Date(v.created_at), "yyyy-MM-dd");
        const country = (v as any).country || "unknown";
        if (!countryDailyMap.has(country)) {
          countryDailyMap.set(country, new Map());
        }
        const dateMap = countryDailyMap.get(country)!;
        dateMap.set(date, (dateMap.get(date) || 0) + 1);
      });

      const countryDailyViews: LocationDailyView[] = [];
      countryDailyMap.forEach((dateMap, location) => {
        dateMap.forEach((views, date) => {
          countryDailyViews.push({ date, location, views });
        });
      });
      countryDailyViews.sort((a, b) => a.date.localeCompare(b.date));

      // City daily views (for trend analysis)
      const cityDailyMap = new Map<string, Map<string, number>>();
      views.forEach(v => {
        const date = format(new Date(v.created_at), "yyyy-MM-dd");
        const city = (v as any).city || "unknown";
        if (!cityDailyMap.has(city)) {
          cityDailyMap.set(city, new Map());
        }
        const dateMap = cityDailyMap.get(city)!;
        dateMap.set(date, (dateMap.get(date) || 0) + 1);
      });

      const cityDailyViews: LocationDailyView[] = [];
      cityDailyMap.forEach((dateMap, location) => {
        dateMap.forEach((views, date) => {
          cityDailyViews.push({ date, location, views });
        });
      });
      cityDailyViews.sort((a, b) => a.date.localeCompare(b.date));

      // Top restaurants
      const restaurantViews = new Map<string, number>();
      views
        .filter(v => v.page_type === "restaurant" && v.target_id)
        .forEach(v => {
          restaurantViews.set(v.target_id!, (restaurantViews.get(v.target_id!) || 0) + 1);
        });

      const topRestaurantIds = Array.from(restaurantViews.entries())
        .sort((a, b) => b[1] - a[1])
        .slice(0, 10);

      // Fetch restaurant names
      let topRestaurants: Array<{ id: string; name: string; views: number }> = [];
      if (topRestaurantIds.length > 0) {
        const { data: restaurants } = await supabase
          .from("restaurants")
          .select("id, name")
          .in("id", topRestaurantIds.map(r => r[0]));

        topRestaurants = topRestaurantIds.map(([id, views]) => ({
          id,
          name: restaurants?.find(r => r.id === id)?.name || "Unknown",
          views,
        }));
      }

      // Top activities
      const activityViews = new Map<string, number>();
      views
        .filter(v => v.page_type === "activity" && v.target_id)
        .forEach(v => {
          activityViews.set(v.target_id!, (activityViews.get(v.target_id!) || 0) + 1);
        });

      const topActivityIds = Array.from(activityViews.entries())
        .sort((a, b) => b[1] - a[1])
        .slice(0, 10);

      // Fetch activity titles
      let topActivities: Array<{ id: string; title: string; views: number }> = [];
      if (topActivityIds.length > 0) {
        const { data: activities } = await supabase
          .from("activities")
          .select("id, title_zh, title_en, title_pt")
          .in("id", topActivityIds.map(a => a[0]));

        topActivities = topActivityIds.map(([id, views]) => {
          const activity = activities?.find(a => a.id === id);
          return {
            id,
            title: activity?.title_zh || activity?.title_en || "Unknown",
            views,
          };
        });
      }

      return {
        totalViews,
        uniqueVisitors,
        viewsByPage,
        dailyViews,
        topRestaurants,
        topActivities,
        refererStats,
        countryStats,
        cityStats,
        countryDailyViews,
        cityDailyViews,
      };
    },
  });
};

export default useAnalytics;
