import { useState, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { 
  Eye, 
  Users, 
  TrendingUp, 
  Store, 
  Calendar, 
  BarChart3,
  RefreshCw,
  ArrowUpRight,
  Globe,
  Link2,
  MapPin,
  Building,
  Circle
} from "lucide-react";
import { useAnalytics } from "@/hooks/useAnalytics";
import { useOnlineUsers } from "@/hooks/useOnlineUsers";
import { OnlineUsersDialog } from "@/components/admin/OnlineUsersDialog";
import { 
  LineChart, 
  Line, 
  XAxis, 
  YAxis, 
  CartesianGrid, 
  Tooltip, 
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  BarChart,
  Bar,
  Legend
} from "recharts";
import { format } from "date-fns";
import { Link } from "react-router-dom";

const COLORS = ['hsl(var(--primary))', 'hsl(var(--chart-2))', 'hsl(var(--chart-3))', 'hsl(var(--chart-4))', 'hsl(var(--chart-5))', 'hsl(var(--muted-foreground))'];
const TREND_COLORS = ['hsl(var(--primary))', 'hsl(var(--chart-2))', 'hsl(var(--chart-3))', 'hsl(var(--chart-4))', 'hsl(var(--chart-5))'];

const AdminAnalytics = () => {
  const { t, i18n } = useTranslation();
  const [days, setDays] = useState(30);
  const [selectedCountries, setSelectedCountries] = useState<string[]>([]);
  const [selectedCities, setSelectedCities] = useState<string[]>([]);
  const { data, isLoading, refetch, isRefetching } = useAnalytics(days);
  const { onlineCount, visitorCount, isLoading: isLoadingOnline } = useOnlineUsers();

  const pageTypeLabels: Record<string, string> = {
    home: t("analytics.pageTypes.home", "首页"),
    restaurant: t("analytics.pageTypes.restaurant", "餐厅详情"),
    activity: t("analytics.pageTypes.activity", "活动详情"),
    restaurants: t("analytics.pageTypes.restaurants", "餐厅列表"),
    activities: t("analytics.pageTypes.activities", "活动列表"),
    map: t("analytics.pageTypes.map", "地图"),
    profile: t("analytics.pageTypes.profile", "个人资料"),
    auth: t("analytics.pageTypes.auth", "登录/注册"),
    "join-us": t("analytics.pageTypes.joinUs", "加入我们"),
    other: t("analytics.pageTypes.other", "其他"),
  };

  const refererLabels: Record<string, string> = {
    direct: t("analytics.referer.direct", "直接访问"),
    unknown: t("analytics.referer.unknown", "未知来源"),
  };

  const locationLabels: Record<string, string> = {
    unknown: t("analytics.location.unknown", "未知地区"),
  };

  const getRefererLabel = (domain: string) => {
    return refererLabels[domain] || domain;
  };

  const getLocationLabel = (location: string) => {
    return locationLabels[location] || location;
  };

  // Get unique countries and cities for selection
  const availableCountries = useMemo(() => {
    return data?.countryStats?.slice(0, 10).map(item => item.location) || [];
  }, [data?.countryStats]);

  const availableCities = useMemo(() => {
    return data?.cityStats?.slice(0, 10).map(item => item.location) || [];
  }, [data?.cityStats]);

  // Build country trend data
  const countryTrendData = useMemo(() => {
    if (!data?.countryDailyViews || selectedCountries.length === 0) return [];
    
    const dateMap = new Map<string, Record<string, number>>();
    data.countryDailyViews
      .filter(item => selectedCountries.includes(item.location))
      .forEach(item => {
        if (!dateMap.has(item.date)) {
          dateMap.set(item.date, {});
        }
        dateMap.get(item.date)![item.location] = item.views;
      });
    
    return Array.from(dateMap.entries())
      .map(([date, locations]) => ({ date, ...locations }))
      .sort((a, b) => a.date.localeCompare(b.date));
  }, [data?.countryDailyViews, selectedCountries]);

  // Build city trend data
  const cityTrendData = useMemo(() => {
    if (!data?.cityDailyViews || selectedCities.length === 0) return [];
    
    const dateMap = new Map<string, Record<string, number>>();
    data.cityDailyViews
      .filter(item => selectedCities.includes(item.location))
      .forEach(item => {
        if (!dateMap.has(item.date)) {
          dateMap.set(item.date, {});
        }
        dateMap.get(item.date)![item.location] = item.views;
      });
    
    return Array.from(dateMap.entries())
      .map(([date, locations]) => ({ date, ...locations }))
      .sort((a, b) => a.date.localeCompare(b.date));
  }, [data?.cityDailyViews, selectedCities]);

  const pieData = data?.viewsByPage
    ? Object.entries(data.viewsByPage).map(([key, value]) => ({
        name: pageTypeLabels[key] || key,
        value,
      }))
    : [];

  const refererChartData = data?.refererStats?.slice(0, 10).map(item => ({
    name: getRefererLabel(item.domain),
    value: item.count,
    percentage: item.percentage.toFixed(1),
  })) || [];

  const countryChartData = data?.countryStats?.slice(0, 10).map(item => ({
    name: getLocationLabel(item.location),
    value: item.count,
    percentage: item.percentage.toFixed(1),
  })) || [];

  const cityChartData = data?.cityStats?.slice(0, 10).map(item => ({
    name: getLocationLabel(item.location),
    value: item.count,
    percentage: item.percentage.toFixed(1),
  })) || [];

  const toggleCountry = (country: string) => {
    setSelectedCountries(prev => 
      prev.includes(country) 
        ? prev.filter(c => c !== country)
        : prev.length < 5 ? [...prev, country] : prev
    );
  };

  const toggleCity = (city: string) => {
    setSelectedCities(prev => 
      prev.includes(city) 
        ? prev.filter(c => c !== city)
        : prev.length < 5 ? [...prev, city] : prev
    );
  };

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold">{t("analytics.title", "访问统计")}</h1>
          <p className="text-muted-foreground">{t("analytics.description", "网站访问数据分析")}</p>
        </div>
        <div className="flex items-center gap-2">
          <div className="flex gap-1 flex-wrap">
            <Button
              variant={days === 1 ? "default" : "outline"}
              size="sm"
              onClick={() => setDays(1)}
            >
              {t("analytics.today", "今日")}
            </Button>
            {[7, 30, 90].map((d) => (
              <Button
                key={d}
                variant={days === d ? "default" : "outline"}
                size="sm"
                onClick={() => setDays(d)}
              >
                {d}{t("analytics.days", "天")}
              </Button>
            ))}
            <Button
              variant={days === 0 ? "default" : "outline"}
              size="sm"
              onClick={() => setDays(0)}
            >
              {t("analytics.all", "所有")}
            </Button>
          </div>
          <Button variant="outline" size="sm" onClick={() => refetch()} disabled={isRefetching}>
            <RefreshCw className={`w-4 h-4 mr-1 ${isRefetching ? "animate-spin" : ""}`} />
            {t("common.refresh", "刷新")}
          </Button>
        </div>
      </div>

      {isLoading ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {[...Array(4)].map((_, i) => (
            <Skeleton key={i} className="h-32" />
          ))}
        </div>
      ) : (
        <>
          {/* Summary Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
            {/* Online Users Card */}
            <Card className="border-green-500/50 bg-green-500/5">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium flex items-center gap-2">
                  <Circle className="h-3 w-3 fill-green-500 text-green-500" />
                  {t("analytics.onlineUsers", "在线用户")}
                </CardTitle>
                <Users className="h-4 w-4 text-green-500" />
              </CardHeader>
              <CardContent>
                <div className="flex items-baseline gap-3">
                  <div className="text-2xl font-bold text-green-600 dark:text-green-400">
                    {isLoadingOnline ? '-' : onlineCount}
                  </div>
                  <div className="flex items-center gap-1 text-muted-foreground">
                    <span className="text-sm">+</span>
                    <span className="text-lg font-semibold text-blue-600 dark:text-blue-400">
                      {isLoadingOnline ? '-' : visitorCount}
                    </span>
                    <span className="text-xs text-muted-foreground">{t("analytics.visitors", "访客")}</span>
                  </div>
                </div>
                <div className="mt-2">
                  <OnlineUsersDialog 
                    trigger={
                      <Button variant="ghost" size="sm" className="h-6 px-2 text-xs text-green-600 dark:text-green-400 hover:text-green-700">
                        {t("analytics.viewOnlineUsers", "查看在线用户")} →
                      </Button>
                    }
                  />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">{t("analytics.totalViews", "总访问量")}</CardTitle>
                <Eye className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{data?.totalViews.toLocaleString()}</div>
                <p className="text-xs text-muted-foreground">
                  {days === 0 
                    ? t("analytics.allTime", "所有时间")
                    : days === 1 
                      ? t("analytics.today", "今日")
                      : t("analytics.lastNDays", "最近{{days}}天", { days })
                  }
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">{t("analytics.uniqueVisitors", "独立访客")}</CardTitle>
                <Users className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{data?.uniqueVisitors.toLocaleString()}</div>
                <p className="text-xs text-muted-foreground">
                  {t("analytics.uniqueVisitorDesc", "基于访客ID")}
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">{t("analytics.restaurantViews", "餐厅浏览")}</CardTitle>
                <Store className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{(data?.viewsByPage?.restaurant || 0).toLocaleString()}</div>
                <p className="text-xs text-muted-foreground">
                  {t("analytics.restaurantViewsDesc", "餐厅详情页访问")}
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">{t("analytics.activityViews", "活动浏览")}</CardTitle>
                <Calendar className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{(data?.viewsByPage?.activity || 0).toLocaleString()}</div>
                <p className="text-xs text-muted-foreground">
                  {t("analytics.activityViewsDesc", "活动详情页访问")}
                </p>
              </CardContent>
            </Card>
          </div>

          {/* Charts Row */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Daily Views Chart */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <TrendingUp className="w-5 h-5" />
                  {t("analytics.dailyTrend", "每日访问趋势")}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="h-[300px]">
                  {data?.dailyViews && data.dailyViews.length > 0 ? (
                    <ResponsiveContainer width="100%" height="100%">
                      <LineChart data={data.dailyViews}>
                        <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                        <XAxis 
                          dataKey="date" 
                          tickFormatter={(value) => format(new Date(value), "MM/dd")}
                          className="text-xs"
                        />
                        <YAxis className="text-xs" />
                        <Tooltip 
                          labelFormatter={(value) => format(new Date(value), "yyyy-MM-dd")}
                          contentStyle={{ 
                            backgroundColor: "hsl(var(--background))", 
                            border: "1px solid hsl(var(--border))" 
                          }}
                        />
                        <Line 
                          type="monotone" 
                          dataKey="views" 
                          stroke="hsl(var(--primary))" 
                          strokeWidth={2}
                          name={t("analytics.views", "访问量")}
                        />
                        <Line 
                          type="monotone" 
                          dataKey="uniqueVisitors" 
                          stroke="hsl(var(--chart-2))" 
                          strokeWidth={2}
                          name={t("analytics.uniqueVisitors", "独立访客")}
                        />
                      </LineChart>
                    </ResponsiveContainer>
                  ) : (
                    <div className="h-full flex items-center justify-center text-muted-foreground">
                      {t("analytics.noData", "暂无数据")}
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>

            {/* Page Type Distribution */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <BarChart3 className="w-5 h-5" />
                  {t("analytics.pageDistribution", "页面访问分布")}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="h-[300px]">
                  {pieData.length > 0 ? (
                    <ResponsiveContainer width="100%" height="100%">
                      <PieChart>
                        <Pie
                          data={pieData}
                          cx="50%"
                          cy="50%"
                          labelLine={false}
                          label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                          outerRadius={100}
                          fill="#8884d8"
                          dataKey="value"
                        >
                          {pieData.map((_, index) => (
                            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                          ))}
                        </Pie>
                        <Tooltip 
                          contentStyle={{ 
                            backgroundColor: "hsl(var(--background))", 
                            border: "1px solid hsl(var(--border))" 
                          }}
                        />
                      </PieChart>
                    </ResponsiveContainer>
                  ) : (
                    <div className="h-full flex items-center justify-center text-muted-foreground">
                      {t("analytics.noData", "暂无数据")}
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Referer Statistics */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Globe className="w-5 h-5" />
                {t("analytics.refererStats", "访问来源统计")}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Referer Bar Chart */}
                <div className="h-[300px]">
                  {refererChartData.length > 0 ? (
                    <ResponsiveContainer width="100%" height="100%">
                      <BarChart data={refererChartData} layout="vertical">
                        <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                        <XAxis type="number" className="text-xs" />
                        <YAxis 
                          dataKey="name" 
                          type="category" 
                          width={120}
                          className="text-xs"
                          tick={{ fontSize: 11 }}
                        />
                        <Tooltip 
                          formatter={(value: number, name: string, props: any) => [
                            `${value} (${props.payload.percentage}%)`,
                            t("analytics.views", "访问量")
                          ]}
                          contentStyle={{ 
                            backgroundColor: "hsl(var(--background))", 
                            border: "1px solid hsl(var(--border))" 
                          }}
                        />
                        <Bar dataKey="value" fill="hsl(var(--primary))" radius={[0, 4, 4, 0]} />
                      </BarChart>
                    </ResponsiveContainer>
                  ) : (
                    <div className="h-full flex items-center justify-center text-muted-foreground">
                      {t("analytics.noData", "暂无数据")}
                    </div>
                  )}
                </div>

                {/* Referer List */}
                <div className="space-y-3 max-h-[300px] overflow-y-auto">
                  {data?.refererStats && data.refererStats.length > 0 ? (
                    data.refererStats.map((item, index) => (
                      <div key={item.domain} className="flex items-center justify-between p-2 rounded-lg bg-muted/50">
                        <div className="flex items-center gap-3">
                          <Badge variant={index < 3 ? "default" : "secondary"} className="w-6 h-6 p-0 justify-center">
                            {index + 1}
                          </Badge>
                          <div className="flex items-center gap-2">
                            <Link2 className="w-4 h-4 text-muted-foreground" />
                            <span className="font-medium text-sm truncate max-w-[150px]">
                              {getRefererLabel(item.domain)}
                            </span>
                          </div>
                        </div>
                        <div className="flex items-center gap-3">
                          <span className="text-sm font-medium">{item.count}</span>
                          <Badge variant="outline" className="text-xs">
                            {item.percentage.toFixed(1)}%
                          </Badge>
                        </div>
                      </div>
                    ))
                  ) : (
                    <p className="text-muted-foreground text-center py-8">
                      {t("analytics.noData", "暂无数据")}
                    </p>
                  )}
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Location Statistics */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Country Statistics */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Globe className="w-5 h-5" />
                  {t("analytics.countryStats", "国家/地区统计")}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {/* Country Bar Chart */}
                  <div className="h-[200px]">
                    {countryChartData.length > 0 ? (
                      <ResponsiveContainer width="100%" height="100%">
                        <BarChart data={countryChartData} layout="vertical">
                          <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                          <XAxis type="number" className="text-xs" />
                          <YAxis 
                            dataKey="name" 
                            type="category" 
                            width={100}
                            className="text-xs"
                            tick={{ fontSize: 11 }}
                          />
                          <Tooltip 
                            formatter={(value: number, name: string, props: any) => [
                              `${value} (${props.payload.percentage}%)`,
                              t("analytics.views", "访问量")
                            ]}
                            contentStyle={{ 
                              backgroundColor: "hsl(var(--background))", 
                              border: "1px solid hsl(var(--border))" 
                            }}
                          />
                          <Bar dataKey="value" fill="hsl(var(--chart-2))" radius={[0, 4, 4, 0]} />
                        </BarChart>
                      </ResponsiveContainer>
                    ) : (
                      <div className="h-full flex items-center justify-center text-muted-foreground">
                        {t("analytics.noData", "暂无数据")}
                      </div>
                    )}
                  </div>

                  {/* Country List */}
                  <div className="space-y-2 max-h-[200px] overflow-y-auto">
                    {data?.countryStats && data.countryStats.length > 0 ? (
                      data.countryStats.slice(0, 10).map((item, index) => (
                        <div key={item.location} className="flex items-center justify-between p-2 rounded-lg bg-muted/50">
                          <div className="flex items-center gap-3">
                            <Badge variant={index < 3 ? "default" : "secondary"} className="w-6 h-6 p-0 justify-center">
                              {index + 1}
                            </Badge>
                            <div className="flex items-center gap-2">
                              <Globe className="w-4 h-4 text-muted-foreground" />
                              <span className="font-medium text-sm truncate max-w-[120px]">
                                {getLocationLabel(item.location)}
                              </span>
                            </div>
                          </div>
                          <div className="flex items-center gap-2">
                            <span className="text-sm font-medium">{item.count}</span>
                            <Badge variant="outline" className="text-xs">
                              {item.percentage.toFixed(1)}%
                            </Badge>
                          </div>
                        </div>
                      ))
                    ) : (
                      <p className="text-muted-foreground text-center py-4">
                        {t("analytics.noData", "暂无数据")}
                      </p>
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* City Statistics */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Building className="w-5 h-5" />
                  {t("analytics.cityStats", "城市统计")}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {/* City Bar Chart */}
                  <div className="h-[200px]">
                    {cityChartData.length > 0 ? (
                      <ResponsiveContainer width="100%" height="100%">
                        <BarChart data={cityChartData} layout="vertical">
                          <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                          <XAxis type="number" className="text-xs" />
                          <YAxis 
                            dataKey="name" 
                            type="category" 
                            width={100}
                            className="text-xs"
                            tick={{ fontSize: 11 }}
                          />
                          <Tooltip 
                            formatter={(value: number, name: string, props: any) => [
                              `${value} (${props.payload.percentage}%)`,
                              t("analytics.views", "访问量")
                            ]}
                            contentStyle={{ 
                              backgroundColor: "hsl(var(--background))", 
                              border: "1px solid hsl(var(--border))" 
                            }}
                          />
                          <Bar dataKey="value" fill="hsl(var(--chart-3))" radius={[0, 4, 4, 0]} />
                        </BarChart>
                      </ResponsiveContainer>
                    ) : (
                      <div className="h-full flex items-center justify-center text-muted-foreground">
                        {t("analytics.noData", "暂无数据")}
                      </div>
                    )}
                  </div>

                  {/* City List */}
                  <div className="space-y-2 max-h-[200px] overflow-y-auto">
                    {data?.cityStats && data.cityStats.length > 0 ? (
                      data.cityStats.slice(0, 10).map((item, index) => (
                        <div key={item.location} className="flex items-center justify-between p-2 rounded-lg bg-muted/50">
                          <div className="flex items-center gap-3">
                            <Badge variant={index < 3 ? "default" : "secondary"} className="w-6 h-6 p-0 justify-center">
                              {index + 1}
                            </Badge>
                            <div className="flex items-center gap-2">
                              <MapPin className="w-4 h-4 text-muted-foreground" />
                              <span className="font-medium text-sm truncate max-w-[120px]">
                                {getLocationLabel(item.location)}
                              </span>
                            </div>
                          </div>
                          <div className="flex items-center gap-2">
                            <span className="text-sm font-medium">{item.count}</span>
                            <Badge variant="outline" className="text-xs">
                              {item.percentage.toFixed(1)}%
                            </Badge>
                          </div>
                        </div>
                      ))
                    ) : (
                      <p className="text-muted-foreground text-center py-4">
                        {t("analytics.noData", "暂无数据")}
                      </p>
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Location Trend Analysis */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Country Trend */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <TrendingUp className="w-5 h-5" />
                  {t("analytics.countryTrend", "国家/地区访问趋势")}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {/* Country Selection */}
                  <div className="flex flex-wrap gap-2">
                    {availableCountries.map((country, index) => (
                      <Button
                        key={country}
                        variant={selectedCountries.includes(country) ? "default" : "outline"}
                        size="sm"
                        onClick={() => toggleCountry(country)}
                        className="text-xs"
                        style={selectedCountries.includes(country) ? { 
                          backgroundColor: TREND_COLORS[selectedCountries.indexOf(country) % TREND_COLORS.length] 
                        } : {}}
                      >
                        {getLocationLabel(country)}
                      </Button>
                    ))}
                  </div>
                  <p className="text-xs text-muted-foreground">
                    {t("analytics.selectLocationsHint", "选择最多5个地区查看趋势（点击选择/取消）")}
                  </p>

                  {/* Country Trend Chart */}
                  <div className="h-[250px]">
                    {countryTrendData.length > 0 ? (
                      <ResponsiveContainer width="100%" height="100%">
                        <LineChart data={countryTrendData}>
                          <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                          <XAxis 
                            dataKey="date" 
                            tickFormatter={(value) => format(new Date(value), "MM/dd")}
                            className="text-xs"
                          />
                          <YAxis className="text-xs" />
                          <Tooltip 
                            labelFormatter={(value) => format(new Date(value), "yyyy-MM-dd")}
                            contentStyle={{ 
                              backgroundColor: "hsl(var(--background))", 
                              border: "1px solid hsl(var(--border))" 
                            }}
                          />
                          <Legend />
                          {selectedCountries.map((country, index) => (
                            <Line 
                              key={country}
                              type="monotone" 
                              dataKey={country}
                              stroke={TREND_COLORS[index % TREND_COLORS.length]}
                              strokeWidth={2}
                              name={getLocationLabel(country)}
                              dot={false}
                            />
                          ))}
                        </LineChart>
                      </ResponsiveContainer>
                    ) : (
                      <div className="h-full flex items-center justify-center text-muted-foreground">
                        {selectedCountries.length === 0 
                          ? t("analytics.selectLocations", "请选择地区查看趋势")
                          : t("analytics.noData", "暂无数据")}
                      </div>
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* City Trend */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <TrendingUp className="w-5 h-5" />
                  {t("analytics.cityTrend", "城市访问趋势")}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {/* City Selection */}
                  <div className="flex flex-wrap gap-2">
                    {availableCities.map((city, index) => (
                      <Button
                        key={city}
                        variant={selectedCities.includes(city) ? "default" : "outline"}
                        size="sm"
                        onClick={() => toggleCity(city)}
                        className="text-xs"
                        style={selectedCities.includes(city) ? { 
                          backgroundColor: TREND_COLORS[selectedCities.indexOf(city) % TREND_COLORS.length] 
                        } : {}}
                      >
                        {getLocationLabel(city)}
                      </Button>
                    ))}
                  </div>
                  <p className="text-xs text-muted-foreground">
                    {t("analytics.selectLocationsHint", "选择最多5个地区查看趋势（点击选择/取消）")}
                  </p>

                  {/* City Trend Chart */}
                  <div className="h-[250px]">
                    {cityTrendData.length > 0 ? (
                      <ResponsiveContainer width="100%" height="100%">
                        <LineChart data={cityTrendData}>
                          <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                          <XAxis 
                            dataKey="date" 
                            tickFormatter={(value) => format(new Date(value), "MM/dd")}
                            className="text-xs"
                          />
                          <YAxis className="text-xs" />
                          <Tooltip 
                            labelFormatter={(value) => format(new Date(value), "yyyy-MM-dd")}
                            contentStyle={{ 
                              backgroundColor: "hsl(var(--background))", 
                              border: "1px solid hsl(var(--border))" 
                            }}
                          />
                          <Legend />
                          {selectedCities.map((city, index) => (
                            <Line 
                              key={city}
                              type="monotone" 
                              dataKey={city}
                              stroke={TREND_COLORS[index % TREND_COLORS.length]}
                              strokeWidth={2}
                              name={getLocationLabel(city)}
                              dot={false}
                            />
                          ))}
                        </LineChart>
                      </ResponsiveContainer>
                    ) : (
                      <div className="h-full flex items-center justify-center text-muted-foreground">
                        {selectedCities.length === 0 
                          ? t("analytics.selectLocations", "请选择地区查看趋势")
                          : t("analytics.noData", "暂无数据")}
                      </div>
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

        </>
      )}
    </div>
  );
};

export default AdminAnalytics;
